import React, { useState, useEffect } from "react";
import Header from "../components/Header";
import ControlPanel from "../components/ControlPanel.js";
import { useDispatch } from "react-redux";
import { setUser } from "../redux/features/authSlice.js";

import "./Home.css";

function Home(props) {
  //checking local storage for user profile
  const dispatch = useDispatch();
  const user = JSON.parse(localStorage.getItem("profile"));

  useEffect(() => {
    dispatch(setUser(user));
    if (user != null) {
      props.setIsLogged(true);
    } else {
      props.setIsLogged(false);
    }
  }, []);

  props.isDebug &&
    console.log(
      "[Home.js]:Rendered\n  isDebug:",
      props.isDebug,
      "\n  isLogged:",
      props.isLogged
    );

  return (
    <div className="main">
      <div className="first-page">
        <div className="background">
          <Header isLogged={props.isLogged} isDebug={props.isDebug} />
          <br />
          <br />
          {
            props.isLogged && console.log("CONTROL PANEL") //<ControlPanel isLogged={props.isLogged} isDebug={props.isDebug} />
          }
        </div>
      </div>
    </div>
  );
}

export default Home;
