import React, { useState, useEffect } from "react";
import {
  MDBCard,
  MDBCardBody,
  MDBInput,
  MDBCardFooter,
  MDBValidation,
  MDBBtn,
  MDBIcon,
  MDBSpinner,
} from "mdb-react-ui-kit";
import { Link, useNavigate } from "react-router-dom";
import { useDispatch, useSelector } from "react-redux";
import { toast } from "react-toastify";
import { GoogleLogin } from "react-google-login";
import { gapi } from "gapi-script";
import { googleSignIn, login } from "../redux/features/authSlice";

//google app client id
const clientId =
  "468812680155-475r5qtishm1g7lfboj9noc1dfusqcag.apps.googleusercontent.com";

const initialState = {
  email: "",
  password: "",
};

const Login = (props) => {
  console.log("[Login.js]:Rendered");
  const { loading, error } = useSelector((state) => ({ ...state.auth }));
  const dispatch = useDispatch();
  const navigate = useNavigate();

  //login form
  const [formValue, setFormValue] = useState(initialState);
  const { email, password } = formValue;

  //google api client init
  useEffect(() => {
    const initClient = () => {
      gapi.client.init({
        clientId: clientId,
        scope: "",
      });
    };
    gapi.load("client:auth2", initClient);
  }, []);

  //toastify errors
  useEffect(() => {
    error && toast.error(error);
  }, [error]);

  //submit form button
  const handleSubmit = (event) => {
    event.preventDefault();
    if (email && password) {
      dispatch(login({ formValue, navigate, toast }));
      props.isDebug && console.log("[Login.js]:Successfull login with email");
    }
  };

  //input change handle
  const onInputChange = (e) => {
    let { name, value } = e.target;
    setFormValue({ ...formValue, [name]: value });
  };

  //success google login
  const onSuccess = (res) => {
    console.log("Response body:", res);
    try {
      const email = res?.profileObj?.email;
      const name = res?.profileObj?.name;
      const avatar = res?.profileObj?.imageUrl;
      const token = res?.tokenId;
      const googleId = res?.googleId;
      const result = { email, name, avatar, token, googleId };

      props.isDebug && console.log("[Login.js]:Google user login");
      dispatch(googleSignIn({ result, navigate, toast }));
    } catch (error) {
      console.log(error);
    }
  };

  //google login fail
  const onFailure = (error) => {
    toast.error(error);
  };

  return (
    <div
      style={{
        margin: "auto",
        padding: "15px",
        maxWidth: "450px",
        alignContent: "center",
        marginTop: "120px",
      }}
    >
      <MDBCard alignment="center">
        <MDBIcon fas icon="user-circle" className="fa-2x" />
        <h5>Sign In</h5>
        <MDBCardBody>
          <MDBValidation onSubmit={handleSubmit} noValidate className="row g-3">
            <div className="col-md-12">
              <MDBInput
                label="Email"
                type="email"
                value={email}
                name="email"
                onChange={onInputChange}
                required
                invalid
                validation="Please provide your email"
              />
            </div>
            <div className="col-md-12">
              <MDBInput
                label="Password"
                type="password"
                value={password}
                name="password"
                onChange={onInputChange}
                required
                invalid
                validation="Please provide your password"
              />
            </div>
            <div className="col-12">
              <MDBBtn style={{ width: "100%" }} className="mt-2">
                {loading && (
                  <MDBSpinner
                    size="sm"
                    role="status"
                    tag="span"
                    className="me-2"
                  />
                )}
                Login
              </MDBBtn>
            </div>
          </MDBValidation>
          <br />
          <GoogleLogin
            clientId={clientId}
            buttonText="Sign in with Google"
            onSuccess={onSuccess}
            onFailure={onFailure}
            cookiePolicy={"single_host_origin"}
            isSignedIn={false}
          />
        </MDBCardBody>
        <MDBCardFooter>
          <Link to="/register">
            <p>Don't have an account ? Sign Up</p>
          </Link>
        </MDBCardFooter>
      </MDBCard>
    </div>
  );
};

export default Login;
