import React, { useState } from "react";
import { useSelector, useDispatch } from "react-redux";
import { setLogout } from "../redux/features/authSlice";
import {
  MDBNavbar,
  MDBContainer,
  MDBIcon,
  MDBNavbarNav,
  MDBNavbarItem,
  MDBNavbarLink,
  MDBNavbarToggler,
  MDBCollapse,
  MDBNavbarBrand,
} from "mdb-react-ui-kit";
import "./Header.css";

const Header = (props) => {
  const [tgglrShow, setTgglrShow] = useState(false);
  const { user } = useSelector((state) => ({ ...state.auth }));
  const isLogged = props.isLogged;
  const dispatch = useDispatch();

  const handleLogout = () => {
    dispatch(setLogout());
  };

  props.isDebug &&
    console.log(
      "[Header.js]:Rendered\n  isDebug:",
      props.isDebug,
      "\n  isLogged:",
      props.isLogged,
      ":",
      isLogged
    );

  return (
    <MDBNavbar expand="md" fixed="top">
      <MDBContainer fluid>
        <MDBNavbarBrand
          href="/#"
          onClick={() => {
            console.log("[Header.js]:Brand_OnClick\n  Logged:", isLogged);
          }}
        >
          GreenBox
        </MDBNavbarBrand>
        <MDBNavbarToggler
          className="bars"
          type="button"
          aria-expanded="false"
          aria-label="Toggle expanded"
          aria-controls="navbarScroll"
          onClick={() => {
            setTgglrShow(!tgglrShow);
          }}
        >
          <MDBIcon icon="bars" fas color="white" />
        </MDBNavbarToggler>
        <MDBCollapse show={tgglrShow} navbar>
          <MDBNavbarNav
            fullWidth="false"
            className="justify-content-end mb-2 mb-lg-0"
          >
            {user?.result?._id && (
              <>
                <MDBNavbarItem>
                  <MDBIcon
                    far
                    color="white"
                    icon="user-circle"
                    className="fa-2x nav-item"
                  ></MDBIcon>
                </MDBNavbarItem>
                <h5 className="header-text">{user.result.name}</h5>
              </>
            )}
            {/*check response for info
            if data exist set loggedin */}
            {user?.result?._id ? (
              <MDBNavbarItem>
                <MDBNavbarLink href="/">
                  <p onClick={handleLogout}>Logout</p>
                </MDBNavbarLink>
              </MDBNavbarItem>
            ) : (
              <>
                <MDBNavbarItem>
                  <MDBNavbarLink href="/register">
                    <p>Register</p>
                  </MDBNavbarLink>
                </MDBNavbarItem>
                <MDBNavbarItem>
                  <MDBNavbarLink href="/login">
                    <p>Login</p>
                  </MDBNavbarLink>
                </MDBNavbarItem>
              </>
            )}
          </MDBNavbarNav>
        </MDBCollapse>
      </MDBContainer>
    </MDBNavbar>
  );
};

export default Header;
